/*
Responsive Typography
*/
.ratio-block {
  border: 1px solid grey;
  background-color: silver;
  padding: 10px;
  position: relative; }
  .ratio-block--margin-top {
    margin-top: 10px; }
  .ratio-block--1x1 {
    flex: 1 0 auto; }
  .ratio-block--1x1:after {
    content: "";
    display: block;
    padding-bottom: 100%; }
  .ratio-block--4x3 {
    flex: 1 1 auto; }
  .ratio-block--4x3:after {
    content: "";
    display: block;
    padding-bottom: 75%; }
  .ratio-block--16x9 {
    flex: 1 1 auto; }
  .ratio-block--16x9:after {
    content: "";
    display: block;
    padding-bottom: 56.25%; }
  .ratio-block__content {
    padding: 0.5rem;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }

.line-height-grid {
  position: absolute;
  background-image: repeating-linear-gradient(transparent, transparent 7px, #9198e5 7px, #9198e5 8px);
  height: 100%;
  width: 100%; }

