/*------------------------------------*
	SASS FONT SIZE VARIABLES
*------------------------------------*/
/*------------------------------------*\
  Main Body
\*------------------------------------*/
body {
  font-family: lato-regular, lato, Helvetica, Arial, sans-serif; }

/*------------------------------------*\
  HEADINGS
\*------------------------------------*/
/*Further Reading: http:/csswizardry.com/2012/02/pragmatic-practical-font-sizing-in-css/ */
h1, h2, h3, h4, h5 {
  font-family: lato-bold, lato, Helvetica, Arial, sans-serif;
  font-style: normal; }

h1, .text-size-x-large {
  font-size: 2.5rem;
  line-height: 1.2; }

h2, .text-size-large {
  font-size: 1rem;
  line-height: 1.5; }

h3, .text-size-medium {
  font-sxize: 0.875rem;
  line-height: 1.43;
  padding-bottom: 4px; }

h4, .text-size-small {
  font-size: 0.75rem;
  line-height: 1.67;
  padding-bottom: 6px; }

h5, .text-size-x-small {
  font-size: 0.625rem;
  line-height: 1.6; }

p, .size-p {
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.43; }

strong {
  font-weight: 400; }

/* Components */
.wrapper {
  @apply flex max-w-2xl w-full m-auto; }

.sidebar {
  @apply hidden p-4 bg-black; }

@screen md {
  .sidebar {
    @apply block; } }
  .sidebar__logo {
    @apply block; }

.app-menu {
  @apply list-reset flex flex-col; }
  .app-menu__item {
    @apply py-4; }
  .app-menu__link {
    @apply rounded-full bg-white; }

.main-content {
  @apply flex-1; }

.header {
  @apply flex justify-between; }
  .header__column {
    @apply flex; }
  .header__sidebar-toggle {
    @apply p-4; }

@screen md {
  .header__sidebar-toggle {
    @apply hidden; } }

.search-form {
  @apply p-4; }
  .search-form__input {
    @apply hidden; }

@screen md {
  .search-form__input {
    @apply inline-block; } }

.user-nav {
  @apply flex; }
  .user-nav__icon-button {
    @apply block p-4; }
  .user-nav__menu {
    @apply list-reset; }
  .user-nav__menu-link {
    @apply p-4 block; }

.hero-carousel {
  @apply flex flex-col items-start hint-of-mauve-pansy p-4; }
  .hero-carousel__content {
    @apply w-full; }

@screen md {
  .hero-carousel__content {
    @apply w-2/3; } }
  .hero-carousel__cta {
    @apply ballyhoo text-white p-4; }

.plugins-store {
  @apply p-4 nautical; }
  .plugins-store__options-wrap {
    @apply flex justify-between texture--white-trans mb-4; }
  .plugins-store__select-menu {
    @apply m-4 p-4 texture--white-trans; }
  .plugins-store__filter-menu-toggle {
    @apply flex items-center text-white p-4;
    font-size: 1rem; }

@screen md {
  .plugins-store__filter-menu-toggle {
    @apply hidden; } }
  .plugins-store__filter-menu {
    @apply list-reset hidden; }

@screen md {
  .plugins-store__filter-menu {
    @apply flex; } }
  .plugins-store__filter-menu-link {
    @apply text-white block h-full flex items-center px-4 border-b-4; }
  .plugins-store__grid {
    @apply flex flex-wrap -m-2; }
  .plugins-store__grid-item {
    @apply w-full p-2; }

@screen md {
  .plugins-store__grid-item {
    @apply w-1/2; } }

@screen lg {
  .plugins-store__grid-item {
    @apply w-1/4; } }

.plugin-listing {
  @apply ratio-block--1x1 texture--white-trans ratio-block; }
  .plugin-listing__content {
    @apply flex justify-center flex-col items-center ratio-block__content; }
  .plugin-listing__title {
    @apply text-white;
    font-size: 0.875rem; }
  .plugin-listing__stars {
    @apply my-4;
    font-size: 0.75rem; }
